<template>
    <div class="hello">
        <div class="home__container">
            <div class="home__title">
                Address Profile: {{addressProfile}}
            </div>
            <div class="home__title">
                Address Wallet: {{addressWallet}}
            </div>
            <div class="home__title">
                Full Name: {{fullName}}
            </div>
            <div class="home__title">
                KYC: {{KYC}}
            </div>
            <br>
            <br>
            <br>
            <br>
            <div class="home__title">
                Confirmations: {{confirmations}}
            </div>
            <div class="home__title">
                Metric Names: {{nameMetrics}}
            </div>
            <br>
            <br>
            <br>
            <br>
            <div class="home__title">
                Metrics:
            </div>
            <div class="home__cards">
                <div class="card" v-for="obj, index in metrics" :key="index" style="width: 18rem;">
                    <div class="card-body">
                        <h5 class="card-title">{{obj.metricName}}</h5>
                        <p class="card-text">{{obj.result}}</p>
                    </div>
                </div>
            </div>
            
        </div>
        <button @click="handleBack" type="button" class="back btn btn-warning">BACK</button>

    </div>
</template>

<script>
import router from '@/router'
import {
  fetchContractData
} from '@/utils/web3.js';
import { 
    abiDataRouter,
  abiProfileContract,
} from '@/utils/abi.js'
import { 
  ADDR_DATA_ROUTER 
} from '@/utils/addresses.js'
import { 
  methodsByType
} from '@/utils/metricTypes.js'


export default {    
    name: 'ProfileAddress',
    async created() {
        this.addressProfile = this.$route.params.address
        this.addressWallet = await fetchContractData(abiProfileContract, this.addressProfile, 'addressWallet', [])
        this.fullName = await fetchContractData(abiProfileContract, this.addressProfile, 'fullName', [])
        this.KYC = await fetchContractData(abiProfileContract, this.addressProfile, 'KYC', [])
        const lengthConfirmations = await fetchContractData(abiProfileContract, this.addressProfile, 'getLengthConfirmations', [])
        const lengthMetricNames = await fetchContractData(abiProfileContract, this.addressProfile, 'getLengthMetricNames', [])
        if(Number(lengthConfirmations)) {
            this.confirmations = await fetchContractData(abiProfileContract, this.addressProfile, 'getSliceConfirmations', [0, lengthConfirmations])
        }
        if(Number(lengthMetricNames)) {
            this.nameMetrics = await fetchContractData(abiProfileContract, this.addressProfile, 'getSliceMetricNames', [0, lengthMetricNames])
            for(let i = 0; i < this.nameMetrics.length; i++) {
                const metricType = await fetchContractData(abiDataRouter, ADDR_DATA_ROUTER, 'getTypeMetricByName', [this.nameMetrics[i]])
                const getMethod = methodsByType[metricType];
                const result = await fetchContractData(abiDataRouter, ADDR_DATA_ROUTER, getMethod, [this.addressProfile, this.nameMetrics[i]])
                this.metrics.push({
                    result,
                    metricName: this.nameMetrics[i]
                });
            }
                
        }
    },
    data() {
        return {
            addressProfile: '',
            fullName: '',
            addressWallet: '',
            KYC: '',
            confirmations: '',
            nameMetrics: '',
            metrics: []
        }
    },
    methods: {
        handleBack() {
            router.push('/');
        }
    }
}
</script>

<style scoped>
.back {
    position: absolute;
    left: 40px;
    top: 60px;
}
.home__container {
    width: 900px;
    margin: 0 auto;   
}
.home__title {
    margin-top: 30px;
    margin-bottom: 30px;
    font-size: 20px;
}
.home__cards {
    display: flex;
    justify-content: space-between;
    align-content: space-between;
    flex-wrap: wrap;
}
</style>